<template>
  <div
    class="position"
    :style="style"
    :class="[position.pos, { error: !position.player }]"
  >
    <div class="positionName" @click.stop="showSub = true">
      {{ position.pos }}
    </div>
    <div v-if="position.player" class="player">
      {{ position.player.number }}. {{ position.player.name }}
    </div>
    <div
      v-if="position.subs && position.subs.length"
      class="subs"
      :class="{ showSub }"
    >
      <div class="close" @click.stop="showSub = false">&times;</div>
      <div
        v-for="sub in position.subs"
        :key="sub.number"
        class="sub"
        @click.stop="changePlayer(sub)"
      >
        {{ sub.number }}. {{ sub.name }}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    position: Object,
  },
  data() {
    return {
      showSub: false,
    };
  },
  computed: {
    style() {
      return {
        top: (this.position.y - 1) * 25 + "%",
        left: (this.position.x - 1) * 33.33 + "%",
      };
    },
  },
  methods: {
    changePlayer(player) {
      this.$emit("change", player);
      this.showSub = false;
    },
  },
};
</script>

<style lang="scss" scoped>
.position {
  text-align: center;
  position: absolute;
  width: 33.33%;
  height: 25%;
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
  transform: translateY(20%);

  &.error {
    .positionName {
      animation: blink 1s infinite linear;
      background: gray !important;
    }
  }

  .positionName {
    width: 40px;
    height: 40px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    border: 2px solid white;
    font-weight: bold;
    color: white;
    cursor: pointer;
  }

  &.CF {
    .positionName {
      background: red;
    }
  }

  &.CM,
  &.RM,
  &.LM {
    .positionName {
      background: green;
    }
  }

  &.CB,
  &.RB,
  &.LB {
    .positionName {
      background: blue;
    }
  }

  &.GK {
    .positionName {
      background: orange;
    }
  }

  .player {
    color: white;
    margin-top: 10px;
    white-space: nowrap;
  }

  .subs {
    position: absolute;
    top: 80px;
    background: white;
    padding: 10px;
    border-radius: 10px;
    line-height: 24px;
    display: none;
    cursor: pointer;

    &.showSub {
      display: block;
    }

    .close {
      position: absolute;
      top: -10px;
      right: -10px;
      width: 20px;
      height: 20px;
      background: red;
      color: white;
      border-radius: 50%;
      display: inline-flex;
      align-items: center;
      justify-content: center;
    }
  }
}

@keyframes blink {
  to {
    box-shadow: 0 0 30px 10px red;
  }
}
</style>
