<template>
  <div id="app">
    <template v-if="formation">
      <position
        v-for="(pos, index) in formation"
        :key="index"
        :position="pos"
        @change="changePlayer(index, $event)"
      ></position>

      <div class="buttons">
        <span class="btn" @click="setFormation(formationA)">2-3-1</span>
        <span class="btn" @click="setFormation(formationB)">3-2-1</span>
      </div>
    </template>
  </div>
</template>

<script>
import Position from "./components/Position.vue";
import _ from "lodash";

export default {
  name: "App",
  components: { Position },
  data() {
    return {
      formationA: [
        { pos: "CF", x: 2, y: 1, priority: 1 },
        { pos: "LM", x: 1, y: 2, priority: 0 },
        { pos: "CM", x: 2, y: 2, priority: 2 },
        { pos: "RM", x: 3, y: 2, priority: 0 },
        { pos: "CB", x: 1.5, y: 3, priority: 4 },
        { pos: "CB", x: 2.5, y: 3, priority: 4 },
        { pos: "GK", x: 2, y: 4, priority: 3 },
      ],
      formationB: [
        { pos: "CF", x: 2, y: 1, priority: 1 },
        { pos: "CM", x: 1.5, y: 2, priority: 3 },
        { pos: "CM", x: 2.5, y: 2, priority: 3 },
        { pos: "LB", x: 1, y: 3, priority: 0 },
        { pos: "CB", x: 2, y: 3, priority: 2 },
        { pos: "RB", x: 3, y: 3, priority: 0 },
        { pos: "GK", x: 2, y: 4, priority: 4 },
      ],
      players: [
        { name: "Thành Nguyễn", number: 12, positions: ["CM", "RB"] },
        { name: "Cường Nguyễn", number: 10, positions: ["CF", "CM"] },
        { name: "Đức Mạnh", number: 37, positions: ["CB", "RB", "GK"] },
        { name: "Trung Nguyễn", number: 87, positions: ["LB"] },
        { name: "Hoàng Xuyến", number: 26, positions: ["RB", "LB"] },
        { name: "Hoàng Nguyễn", number: 71, positions: ["CF", "RB", "LB"] },
        { name: "Đàm Ngọc", number: 8, positions: ["LB"] },
        { name: "Vũ", number: 6, positions: ["CM", "CF"] },
        { name: "Hữu Nam", number: 2, positions: ["CB"] },
        { name: "An Đàn", number: 3, positions: ["RB", "LB"] },
        { name: "Dũng Trần", number: 7, positions: ["RB", "LB"] },
        { name: "Nguyễn Nam", number: 5, positions: ["CB", "RB"] },
        { name: "Phúc Trần", number: 1, positions: ["RB", "GK"] },
      ],
      formation: null,
    };
  },
  mounted() {
    this.setFormation(this.formationB, this.players);
  },
  methods: {
    setFormation(formation) {
      const players = this.players;
      players.forEach((player) => {
        if (player.positions.includes("LB")) player.positions.push("LM");
        if (player.positions.includes("RB")) player.positions.push("RM");
        player.positions = _.uniq(player.positions);
      });

      const result = _.orderBy(formation, ["priority"], ["desc"]);
      const options = _.cloneDeep(players);

      for (const position of result) {
        const positionName = position.pos;
        position.player = _.shuffle(options).find((player) =>
          player.positions.includes(positionName)
        );

        if (!position.player) continue;

        position.subs = players.filter(
          (player) =>
            player.number !== position.player.number &&
            player.positions.includes(positionName)
        );

        const index = options.findIndex(
          (player) => player.number === position.player.number
        );

        if (index >= 0) {
          options.splice(index, 1);
        }
      }

      this.formation = result;
    },
    changePlayer(index, player) {
      const newForm = _.cloneDeep(this.formation);

      // Remove that player from other positions
      const lastPosIndex = this.formation.findIndex(
        (position) =>
          position.player && position.player.number === player.number
      );

      const lastPos = this.formation[lastPosIndex];

      if (lastPos) {
        newForm[lastPosIndex] = { ...newForm[lastPosIndex], player: null };

        for (const sub of lastPos.subs) {
          if (!this.isOn(sub)) {
            newForm[lastPosIndex] = { ...newForm[lastPosIndex], player: sub };
            break;
          }
        }
      }

      newForm[index] = { ...newForm[index], player };
      this.formation = newForm;

      // Reload subs
      for (const position of this.formation) {
        position.subs = this.players.filter(
          (player) =>
            player.number !== position.player?.number &&
            player.positions.includes(position.pos)
        );
      }
    },
    isOn(player) {
      return Boolean(
        this.formation.find(
          (pos) => pos.player && pos.player.number === player.number
        )
      );
    },
  },
};
</script>

